* {
  padding: 0;
  margin: 0;
  color: white;
}

body {
  background: linear-gradient(rgba(200, 195, 231, 0.2), rgba(55, 207, 218, 0.2)),
    url("https://image.freepik.com/free-photo/christmas-background-with-balls-space-bottom_23-2147722773.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
  height: 100vh;
  width: 100%;
  backdrop-filter: blur(2px);
}

.App h1 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  font-size: 45px;
}

.container {
  width: 30%;
  height: auto;
  border: none;
  top: 17%;
  right: 33%;
  position: absolute;
  background: rgba(172, 166, 166, 0.2);
  backdrop-filter: blur(8px);
  border-radius: 20px;
  box-shadow: 8px 8px 15px rgba(0, 0, 0, 0.9);
}

input[type="text"] {
  width: 100%;
  color: white;
}
.myInput {
  width: 350px;
  height: 55px;
  margin: 15px 60px;
}

.goplus:hover {
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  width: 25px;
  height: 25px;
  border-radius: 30px;
}

.check_delete {
  padding: 5px 80px;
  text-align: left;
  font-size: 20px;
  margin-bottom: 5px;
}

.deleteIcon {
  padding: 0px 30px;
  top: 2px;
  position: relative;
}

.deleteIcon:hover {
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.9);
  width: 35px;
  height: 35px;
  border-radius: 30px;
}

@media (max-width: 400px) {
  .container {
    width: 300px;
    right: 10%;
    position: absolute;
  }
  .myInput {
    width: 60%;
  }
  .container h1 {
    font-size: 32px;
  }
  .deleteIcon {
    padding: 0px 10px;
    top: 4px;
    position: relative;
  }
}

@media (max-width: 1310px) {
  .myInput {
    width: 40%;
    margin: 15px 100px;
  }
}
